import React, {Component} from 'react'
import {BrowserRouter, Route, Switch} from 'react-router-dom'
import {createMuiTheme, MuiThemeProvider} from '@material-ui/core/styles'
import blue from "@material-ui/core/colors/blue"
import NavBar from "./components/NavBar"
import './css/App.scss'
import Home from "./components"
import UserDetails from "./components/UserDetails"
import PageNotFound from "./components/PageNotFound"
import CssBaseline from '@material-ui/core/CssBaseline'

const light = createMuiTheme({
  palette: {
    primary: {main: '#43a047'},
    secondary: blue,
  },
  typography: {
    useNextVariants: true,
  }
})

const dark = createMuiTheme({
  palette: {
    type: "dark"
  },
  typography: {
    useNextVariants: true,
  }
})


class App extends Component {
  state = {
    theme: dark,
    loading: false,
    buildBaseUrl: 'https://zuhairtaha.github.io/w3-react-hw-github-api-users-search',
    devBaseUrl: ''
  }

  changeTheme = theme => {
    theme === "light"
      ? this.setState({theme: light})
      : this.setState({theme: dark})
  }
  loadingHandler = loading => this.setState({loading})

  homeComponent = props =>
    <Home {...props} onLoading={() => this.loadingHandler}/>


  render() {
    const {theme, loading} = this.state
    const baseUrl = this.state.buildBaseUrl
    return (
      <BrowserRouter>
        <MuiThemeProvider theme={theme}>
          <CssBaseline/>
          <NavBar loading={loading} changeTheme={this.changeTheme}/>
          <div className="container">
            <Switch>
              <Route path={`${baseUrl}/`} exact render={this.homeComponent}/>
              <Route path={`${baseUrl}/user/:username`} component={UserDetails}/>
              <Route component={PageNotFound}/>
            </Switch>
          </div>
        </MuiThemeProvider>
      </BrowserRouter>
    )
  }
}

export default App